/* ------ INPUT BG FILL FIX ------ */

.c-form__field-input:-webkit-autofill,
input:-webkit-autofill,
.c-form__field-input:-webkit-autofill:hover,
input:-webkit-autofill:hover,
.c-form__field-input:-webkit-autofill:focus,
input:-webkit-autofill:focus {
  caret-color: #245792;
  -webkit-text-fill-color: #245792;
  -webkit-box-shadow: 0 0 0px 1000px var(--input-bg-color) inset;
}

/* ------ >INPUT BG FILL FIX ------ */

/* ------ FORM BUTTON ------ */

.c-form__button-shedule-pulse::before {
  content: "";
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  background-color: #3fb63d;
  opacity: 0.5;
  z-index: -2;
  animation: pulse 2s infinite;
  border-radius: 100%;
}

@keyframes pulse {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(2);
    opacity: 0;
  }
}

.c-form__button-submit {
  color: inherit;
}

/* ------ >FORM BUTTON ------ */

//////////////////////
/////// FORM ////////
////////////////////

.c-form__field {
  opacity: 0;
  transform: translateY(4rem);
  transition: transform 600ms ease, opacity 600ms ease;
  position: relative;
  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    right: 0;
    transform-origin: left center;
    transform: scaleX(0);
    transition: transform 800ms ease;
  }
  &::before {
    background-color: #d3deea;
  }
  &::after {
    background-color: #245792;
  }

  .is-finish-load.has-dom-ready .c-footer.is-inview &,
  .is-finish-load.is-loading .c-footer.is-inview & {
    opacity: 1;
    transform: translateY(0);
    &:nth-child(1) {
      transition-delay: 100ms;
      &::before {
        transition-delay: 300ms;
      }
    }
    &:nth-child(2) {
      transition-delay: 300ms;
      &::before {
        transition-delay: 500ms;
      }
    }
    &:nth-child(3) {
      transition-delay: 500ms;
      &::before {
        transition-delay: 700ms;
      }
    }

    &::before {
      transform: scaleX(1);
    }
  }
}

.c-form {
  &__title,
  &__working,
  &__wrapper-bottom {
    opacity: 0;
    transition: transform 600ms ease, opacity 600ms ease;
    transform: translateY(4rem);
  }
  .is-finish-load.has-dom-ready .c-footer.is-inview &,
  .is-finish-load.is-loading .c-footer.is-inview & {
    &__title,
    &__working,
    &__wrapper-bottom {
      opacity: 1;
      transform: translateY(0);
    }
    &__wrapper-bottom {
      transition-delay: 800ms;
    }
  }

  &__field {
    &.active {
      .c-form__enter {
        opacity: 1;
        transform: scale(1);
      }
    }
  }
}
